<template>
    <div id="config-index">
        <public-header title="设置管理"></public-header>
        <div class="content">
            <template v-if="util.checkperm('errander.config')">
                <div class="tip">平台</div>
                <van-cell-group>
                    <van-cell title="基础设置" is-link :to="util.getUrl({path: '/pages/plugin/paotui/config/basic'})"/>
                </van-cell-group>
                <div class="tip">订单相关</div>
                <van-cell-group>
                    <van-cell title="订单设置" is-link :to="util.getUrl({path: '/pages/plugin/paotui/config/order'})"/>
                    <van-cell title="时限要求" is-link :to="util.getUrl({path: '/pages/plugin/paotui/config/timelimit'})"/>
                </van-cell-group>
            </template>
        </div>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		data() {
			return {

			}
		},
		components: {
			publicHeader
		},
		methods: {

		}
	}
</script>

<style>
    #config-index .content .tip{
        padding: 10px 15px;
        font-size: 14px;
        color: #7a7a7a;
    }
</style>


